	module.exports = {"data":{"wordpressPost":{"title":"UCL Qualitative Health Research Symposium 2015","content":"<p>The UCL Division of Psychiatry’s Qualitative Researchers Working Group is working together with the UCL Department of Applied Health Research and the UCL Health Behaviour Research Centre on a one day symposium to discuss questions, and to generate constructive commentary on the contributions that qualitative inquiry can make to understandings of health, illness and care.</p>\n<p>The symposium will include a keynote paper by Dr Sara Shaw, Queen Mary University of London and a panel discussion featuring Professor Paul Higgs, UCL Division of Psychiatry, Professor Naomi Fulop, UCL Department of Applied Health Research, and Professor Richard Watt, UCL Epidemiology and Public Health and will include oral and poster presentations from the UK and abroad.</p>\n<p>The background and context of the day can be found on the blog pages of UCL’s Division of Psychiatry <a href=\"http://blogs.ucl.ac.uk/psychiatry/2015/01/29/ucl-qualitative-health-research-symposium-2015-enriching-qualitative-inquiry-health/\">here</a>.</p>\n<p><em>BMJ Open</em> has a track record of publishing high-quality <a href=\"http://bmjopen.bmj.com/collections/bmj_open_qualitative_research\">qualitative research</a> in medicine and healthcare and we are delighted to be supporting this conference. Shortly afterwards the abstracts will also be published as a supplement to <em>BMJ Open</em>.</p>\n<p>Information about the event and registration details can be found on the UCL Department of Applied Health Research <a href=\"http://www.ucl.ac.uk/dahr/events-publication/ucl_qualitative_health_research_symposium_2015\"><span style=\"text-decoration: underline\">website</span></a>.</p>\n<p>And further details about the symposium programme can be found <a href=\"http://www.ucl.ac.uk/hbrc/qualitative_health_research_symposium_2015_programme\"><span style=\"text-decoration: underline\">here</span></a>.<!--TrendMD v2.4.3--></p>\n"},"site":{"siteMetadata":{"title":"Gatsby Default Starter"}}},"pathContext":{"id":"58fd5535-b0b3-5e6b-98dc-a047e1ce392a"}}

